*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  /* 1 */
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%); }

.container {
  max-width: 1280px;
  padding-right: 12px;
  padding-left: 12px;
  margin-right: auto;
  margin-left: auto; }

@media (max-width: 1024px) {
  .container {
    padding-right: 21px;
    padding-left: 21px; } }

@media (max-width: 767px) {
  .container {
    padding-right: 16px;
    padding-left: 16px; } }

.header {
  position: relative;
  background-image: url("../img/header-bg@1x.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding-top: 40px;
  padding-bottom: 66px; }

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/header-bg@2x.jpg"); } }

.header::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2; }

.header__wrapper-nav {
  display: flex;
  justify-content: flex-start;
  position: relative;
  z-index: 3;
  margin-bottom: 60px; }

.header__logo {
  margin-right: 120px; }

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.header__nav-item {
  margin-right: 72px;
  margin-bottom: 10px; }

.header__nav-link {
  font-size: 18px;
  line-height: 22px;
  color: #ffffff; }

.header__nav-link:hover {
  opacity: 0.8; }

.header__nav-link:active {
  opacity: 0.5; }

.header__contacts {
  margin-left: auto; }

.header__contacts-tel {
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #ffffff; }

.header__contacts-tel:hover {
  opacity: 0.8; }

.header__contacts-tel:active {
  opacity: 0.5; }

.header__contacts-title {
  margin: 0;
  font-size: 18px;
  line-height: 22px;
  text-align: right;
  color: #ffffff; }

.header__title {
  margin-top: 0;
  margin-bottom: 0;
  position: relative;
  color: #ffffff;
  z-index: 3; }

@media (max-width: 1024px) {
  .header__wrapper-nav {
    flex-wrap: wrap; }
  .header__logo {
    order: 1;
    margin-bottom: 30px; }
  .header__nav {
    margin-bottom: 30px;
    order: 2;
    width: 100%; }
  .header__nav-item:last-child {
    margin-right: 0; }
  .header__contacts {
    order: 1; } }

@media (max-width: 767px) {
  .header__logo {
    width: 50px;
    margin-right: 0; }
  .header__nav-list {
    flex-direction: column; }
  .header__contacts-tel {
    font-size: 18px;
    line-height: 19px; }
  .header__contacts-title {
    font-size: 16px; } }

.catalog {
  padding-top: 37px;
  padding-bottom: 83px; }

.catalog__sort-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 38px; }

.catalog__sort-title {
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  margin: 0;
  margin-right: 48px; }

.catalog__sort-btn {
  position: relative;
  padding-right: 30px;
  margin-right: 55px;
  font-size: 18px;
  line-height: 22px;
  color: #000000; }

.catalog__sort-btn:hover {
  opacity: 0.8; }

.catalog__sort-btn:active {
  opacity: 0.5; }

.catalog__sort-btn::after {
  position: absolute;
  content: "";
  width: 18px;
  height: 12px;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../img/arrow.svg");
  fill: #000000; }

.catalog__items-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 68px; }

.catalog__item {
  margin-bottom: 27px;
  position: relative; }

.catalog__item-discount::after {
  position: absolute;
  content: "-40%";
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  left: 38px;
  top: 34px;
  padding: 10px 20px;
  border-radius: 10px;
  color: #ffffff;
  background-color: #e52d2d; }

.catalog__photo {
  position: relative;
  line-height: 0; }

.catalog__photo::before {
  cursor: pointer;
  position: absolute;
  content: "";
  right: 34px;
  top: 32px;
  width: 46px;
  height: 42px;
  background-image: url("../img/like-heart.svg");
  opacity: 0.5;
  z-index: 2; }

.catalog__photo-like::before {
  opacity: 1; }

.catalog__description {
  background-color: #f3f3f3;
  padding: 30px; }

.catalog__description-title {
  margin: 0;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 22px; }

.catalog__description-about {
  display: flex;
  justify-content: space-between;
  margin-bottom: 22px; }

.catalog__description-color {
  margin: 0;
  font-size: 12px;
  line-height: 15px;
  border-left: 4px solid #6ebbd3;
  padding-left: 18px;
  max-width: 89px; }

.catalog__description-age {
  margin: 0;
  font-weight: bold;
  font-size: 16px;
  line-height: 15px;
  max-width: 89px; }

.catalog__description-age-about {
  font-weight: normal;
  font-size: 12px;
  line-height: 15px; }

.catalog__description-legs {
  margin: 0;
  font-weight: bold;
  font-size: 16px;
  line-height: 15px;
  max-width: 89px; }

.catalog__description-legs-about {
  font-weight: normal;
  font-size: 12px;
  line-height: 15px; }

.catalog__price {
  margin: 0;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px; }

.catalog__buy {
  cursor: pointer;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  border: none;
  background-color: #6ebbd3;
  width: 100%;
  height: 65px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px; }

.catalog__buy:hover {
  opacity: 0.8; }

.catalog__buy:active {
  opacity: 0.5; }

.catalog__sold-out {
  background-color: #1f2021;
  cursor: not-allowed; }

.catalog__more {
  display: block;
  cursor: pointer;
  font-size: 14px;
  line-height: 17px;
  width: 380px;
  height: 65px;
  margin-left: auto;
  margin-right: auto;
  background-color: transparent;
  border: 1px solid #cacaca;
  box-sizing: border-box;
  border-radius: 10px; }

.catalog__more:hover {
  opacity: 0.8;
  background-color: #f3f3f3; }

.catalog__more:active {
  opacity: 0.5; }

@media (max-width: 1024px) {
  .catalog__items-wrapper {
    justify-content: space-evenly; } }

@media (max-width: 767px) {
  .catalog__sort-wrapper {
    flex-direction: column;
    align-items: flex-start; }
  .catalog__sort-title {
    margin-bottom: 20px; }
  .catalog__sort-btn {
    margin-bottom: 10px; } }

@media (max-width: 430px) {
  .catalog__item-discount::after {
    top: 10px;
    left: 10px;
    transform: scale(0.7); }
  .catalog__photo::before {
    top: 10px;
    right: 10px;
    transform: scale(0.7); }
  .catalog__description-about {
    flex-direction: column; }
  .catalog__description-color {
    width: 100%;
    max-width: none;
    margin-bottom: 10px; }
  .catalog__description-age {
    width: 100%;
    max-width: none;
    margin-bottom: 10px; }
  .catalog__description-legs {
    width: 100%;
    max-width: none;
    margin-bottom: 10px; }
  .catalog__more {
    width: 100%; } }

.footer {
  display: flex;
  background-color: #1f2021;
  padding: 83px 80px;
  margin-bottom: 114px;
  justify-content: space-between; }

.footer-wrapper-text {
  color: #ffffff; }

.footer__title {
  margin: 0;
  margin-bottom: 26px;
  font-weight: bold;
  font-size: 48px;
  line-height: 59px; }

.footer__text {
  margin: 0;
  width: 400px;
  font-size: 28px;
  line-height: 91.2%; }

.footer__form {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 568px; }

.footer__form-email {
  width: 48%;
  max-width: 300px;
  height: 65px;
  background-color: #ffffff;
  border: none;
  border-radius: 10px;
  padding: 22px 33px; }

.footer__form-email::placeholder {
  color: #000000; }

.footer__form-button {
  width: 48%;
  max-width: 250px;
  height: 65px;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  background-color: #6ebbd3;
  border: none;
  border-radius: 10px;
  cursor: pointer; }

.footer__form-button:hover {
  opacity: 0.8; }

.footer__form-button:active {
  opacity: 0.5; }

.footer__checkbox-label {
  cursor: pointer;
  user-select: none;
  position: relative;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  margin-top: 24px;
  padding-left: 37px; }

.footer__checkbox-label::before {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  background-color: #6ebbd3;
  border-radius: 3px; }

.footer__checkbox:checked ~ .footer__checkbox-label::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 8px;
  top: 50%;
  transform: translateY(-50%);
  left: 5px;
  background-image: url("../img/check-mark.svg"); }

@media (max-width: 1024px) {
  .footer {
    flex-direction: column; }
  .footer-wrapper-text {
    margin-bottom: 30px; }
  .footer__form {
    max-width: none; }
  .footer__form-email {
    max-width: none; }
  .footer__form-button {
    max-width: none; } }

@media (max-width: 767px) {
  .footer {
    padding-top: 40px;
    padding-bottom: 40px;
    padding-right: 16px;
    padding-left: 16px; }
  .footer__title {
    font-size: 32px;
    line-height: 32px; }
  .footer__text {
    font-size: 18px;
    line-height: 19px;
    width: 100%; }
  .footer__form-email {
    width: 100%;
    margin-bottom: 25px; }
  .footer__form-button {
    width: 100%; } }

.up-button {
  position: fixed;
  bottom: 10px;
  right: 10px;
  width: 59px;
  height: 59px;
  background-color: #6ebbd3;
  background-image: url("../img/arrow-white.svg");
  background-size: 18px 12px;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
  opacity: 0;
  display: none; }

.up-button--show {
  opacity: 1;
  display: block; }

@media (max-width: 430px) {
  .up-button {
    transform: scale(0.8);
    bottom: 4px;
    right: 4px; } }
